.card {
  gap: 1rem;
  background-color: #ededed;
  border-radius: 0.5rem;
  box-shadow: 5px 5px 20px #666;
  padding: 1.5rem;
  align-items: center;
}

.simple-slope {
  display: flex;
  flex-direction: column;
}

.confidence-interval,
.codingforg,
.slope {
  display: flex;
  flex-direction: column;
}

.title {
  font-size: 1.3rem;
  text-align: left;
  color: #333;
  width: 95%;
  margin-bottom: 10px;
}

.equation img {
  height: 100px;
  margin-bottom: 10px;
}

.container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.parameters {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 70%;
}

.parameters input {
  font-size: 1.3rem;
}

.parameter {
  display: grid;
  gap: 10px;
  grid-template-columns: 1fr 3fr;
}

.parameter-label {
  text-align: right;
  margin-right: 10px;
}

.outputs {
  display: flex;
  flex-direction: column;
  width: 70%;
  justify-content: center;
  align-items: center;
}

.outputs input {
  font-size: 1.3rem;
}

.output {
  font-size: 2rem;
  padding: 10px;
  border: 1px solid black;
  background-color: #f6e8ff;
  border-radius: 5px;
  width: 100%;
}

.split-2 {
  width: 100%;
  height: 35px;
  align-items: center;
  justify-content: center;
  display: flex;
}

.left,
.right {
  width: 40%;
}

.split-output {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.result {
  font-weight: bold;
}

.output-buttons {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.btn-round {
  background-color: #926aa6;
  border: 1px solid #999;
  color: white;
  padding: 5px;
  border-radius: 5px;
  font-size: 1.2rem;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1);
}

.output-buttons button {
  background-color: #926aa6;
  border: 1px solid #999;
  color: white;
  padding: 10px;
  border-radius: 5px;
  font-size: 1.2rem;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1);
}

.calculate {
  display: flex;
  flex-direction: row;
  gap: 100px;
  justify-content: center;
}

.card-container {
  grid-column: span 2;
  background-color: #d1d1d1;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  border-radius: 0.5rem;
  padding: 2rem;
  box-shadow: 5px 5px 20px #666;
}

.container-header {
  grid-column: span 2;
  font-size: 2rem;
}

.ANOVA,
.ANOVA2 {
  grid-column: span 2;
}

.links {
  grid-column: span 2;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 20px;
  background-color: #eee;
  border-radius: 7px;
  margin-top: -20px;
}

.links a {
  color: blue;
}

.anova-table-input {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 0;
}

.table-row {
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.table-header {
  background-color: #ccc;
  padding: 0.5rem;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
}

.last-row {
  border-bottom: 1px solid black;
}

.table-input {
  font-size: 1.1rem;
}

.anova-parameters {
  display: flex;
  width: 50%;
  align-items: center;
  justify-content: center;
}

.anova-inputs {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 10;
  margin-top: 20px;
}

.anova-buttons {
  display: flex;
  width: 100%;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 30px;
}

.anova-buttons button {
  background-color: #926aa6;
  border: 1px solid #999;
  color: white;
  padding: 10px;
  border-radius: 5px;
  font-size: 1.2rem;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1);
}

.anova-outputs {
  margin-bottom: 20px;
}

.extra-values {
  display: flex;
  flex-direction: row;
  padding: 10px;
  justify-content: center;
  align-items: center;
}

.extra-values input {
  font-size: 1.2rem;
  width: 80%;
}

.overline {
  text-decoration: overline;
}
